<template>
  <div id="app">
    <div class="container">
      <h1>예약 시스템</h1>
      <!-- prevent: 링크차단기능 
        폼 제출 시, 페이지 새로고침 방지하고 addReservation 메서드 호출 -->
      <form @submit.prevent="addReservation" class="reservation-form">
        <div class="form-group">
          <!-- label의 for과 input의 id는 동일 -->
          <label for="name">이름 </label>
          <!-- 양방향 데이터바인딩을 사용하여 필드에 입력한 값을 newReservation.name 데이터 속성과 연결
           required: 사용자가 입력필드를 비워두지 않도록 함 -->
          <input type="text" id="name" v-model="newReservation.name" required />
        </div>

        <div class="form-group">
          <label for="date">날짜 </label>
          <input type="date" id="date" v-model="newReservation.date" required />
        </div>

        <button type="submit" class="btn-submit">예약</button>

        <h2>예약목록</h2>
        <ul class="reservation-list">
          <li class="reservation-item" v-for="(reservation, index) in reservations" :key="index">
            <span class="reservation-text">
              {{ reservation.date }} - {{ reservation.name }}
            </span>
            <button class="btn-delete" @click="removeReservation(index)">
              삭제
            </button>
          </li>
        </ul>

      </form>
    </div>
  </div>
</template>

<script>
  export default {
    // data(): 컴포넌트의 데이터 속성들을 반환하는 함수
    // 이 데이터는 컴포넌트에서 사용 가능한 변수들. 컴포넌트의 상태 관리
    data() {
      return {
        // 새로 추가할 예약정보 저장하는 곳 - newReservation
        newReservation : {
          name: "", // 예약지 이름 저장 변수를 빈 문자열로 초기화
          date: "", // 예약한 날자 저장 변수를 빈 문자열로 초기화
        },
        // 모든 예약정보를 저장하는 배열
        // 각 예약은 newReservation 객체와 같은 형식으로 배열에 저장됨
        reservations : [], 
      };
    },
    methods : {
      addReservation() {
        // 조건문 사용하여 이름과 날짜 입력되었는지 확인
        if (this.newReservation.name && this.newReservation.date) {
          // ...: 예약 객체 복제. 새로운 예약이 유효하면 reservations 배열에 예약정보 추가
          this.reservations.push({...this.newReservation});
          this.newReservation.name = ""; // 사용자가 입력한 이름
          this.newReservation.date = ""; // 사용자가 입력한 날짜
        } else {
          // 비어있으면 경고창
          alert("모든 필드를 입력해주세요.");
        }
      },
      // 특정 인덱스 예약 삭제 함수
      removeReservation(index) {
        // .splice - 배열 메서드 : 삭제, 추가, 교체 시 사용
        // 배열에서 index 위치에 있는 1개의 항목 삭제
        this.reservations.splice(index, 1);
      },

    },
  };
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap');
* {
  margin: 0;
  padding: 0;
}
#app {
  background-color: #edebeb;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Nanum Gothic", sans-serif;
  padding: 20px;
}
#app .container {
  background-color: #fff;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.2);
  max-width: 500px;
  width: 100%;
  text-align: center;
}
#app .container h1 {
  text-align: center; margin-bottom: 20px;
  font-size: 2rem;
  color: #333;
}
#app .container h2 {
  font-size: 1.5rem;
  color: #555;
  margin-top: 30px;
  margin-bottom: 20px;
}
#app .container .reservation-form {
  margin-bottom: 20px;
}
#app .container .reservation-form .form-group {
  margin-bottom: 15px;
  text-align: left;
}
#app .container .reservation-form .form-group label {
  display: block;
  font-weight: bold;
  color: #555;
  margin-bottom: 5px;
}
#app .container .reservation-form .form-group input {
  width: 100%;
  padding: 10px;
  border: 2px solid #ddd;
  font-size: 1rem;
  color: #333;
  box-sizing: border-box;
  background-color: #f7f7f7;
  border-radius: 5px;
}
#app .container .reservation-form .form-group input:focus {
  border: 2px solid #19abff;
  outline: none;
  background-color: #fff;
}
button {
  cursor: pointer;
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s;
  padding: 10px 20px;
}
.btn-submit {
  background-color: #19abff;
  width: 100%;
}
.btn-delete {
  background-color: #f44336;
}
.btn-delete:hover {
  background-color: #d32f2f;
}
.btn-delete {
background-color:#f44336;
padding:10px 15px;
margin-left: 20px;

}
/* 예약목록 */
.reservation-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.reservation-list .reservation-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding:10px 15px;
  margin-bottom: 10px;
  border:1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  box-sizing: border-box; 
 
}
.reservation-list .reservation-item .reservation-text {
  font-size: 1rem;
  color: #333;
}

</style>